				}
				this.$nextTick(() => {
					this.$refs.kucunpandianCrossaddOrUpdate.init(row.id,type);
				});
			},


//统计接口
    chartDialog1() {
      this.chartVisiable1 = !this.chartVisiable1;
      this.$nextTick(()=>{
        var shuliangChart1 = echarts.init(document.getElementById("shuliangChart1"),'macarons');
        this.$http({
            url: `huowuxinxi/value/huowumingcheng/shuliang`,
            method: "get",
        }).then(({ data }) => {
            if (data && data.code === 0) {
                let res = data.data;
                let xAxis = [];
                let yAxis = [];
                let pArray = []
                for(let i=0;i<res.length;i++){
                    xAxis.push(res[i].huowumingcheng);
                    yAxis.push(parseFloat((res[i].total)));
                    pArray.push({
                        value: parseFloat((res[i].total)),
                        name: res[i].huowumingcheng
                    })
                }
                var option = {};
                option = {
                        title: {
                            text: '库存统计',
                            left: 'center'
                        },
                        legend: {
                          orient: 'vertical',
                          left: 'left'
                        },
                        tooltip: {
                          trigger: 'item',
                          formatter: '{b} : {c} ({d}%)'
                        },
                        series: [
                            {
                                type: 'pie',
                                radius: ['25%', '55%'],
                                center: ['50%', '60%'],
                                data: pArray,
                                emphasis: {
                                    itemStyle: {
                                        shadowBlur: 10,
                                        shadowOffsetX: 0,
                                        shadowColor: 'rgba(0, 0, 0, 0.5)'
                                    }
                                }
                            }
                        ]
                };
                // 使用刚指定的配置项和数据显示图表。
                shuliangChart1.setOption(option);
                  //根据窗口的大小变动图表
                window.onresize = function() {
                    shuliangChart1.resize();
                };
            }
        });
      })
    },




    init () {
          this.$http({
            url: `option/huowuleixing/huowuleixing`,
            method: "get"
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.huowuleixingOptions = data.data;
            } else {
              this.$message.error(data.msg);
            }
          });
          this.$http({
            url: `option/cangkuxinxi/cangkuhao`,
            method: "get"
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.cangkuhaoOptions = data.data;
            } else {
              this.$message.error(data.msg);
            }
          });
          let urlshuliang = 'huowuxinxi/remind/shuliang/1';
		urlshuliang+=`?remindend=20`,
		
            	urlshuliang+=`&remindstart=0`,

          this.$http({
            url: urlshuliang,
            method: "get",
          }).then(({ data }) => {
            if (data && data.code == 0 && data.count>0 ) {
              this.$alert(`库存有${data.count}条记录到达预警`)
            }
          });
    },
    search() {
      this.pageIndex = 1;
      this.getDataList();
    },

    // 获取数据列表
    getDataList() {
      this.dataListLoading = true;
      let params = {
        page: this.pageIndex,
        limit: this.pageSize,
        sort: 'id',
        order: 'desc',
      }
           if(this.searchForm.huowumingcheng!='' && this.searchForm.huowumingcheng!=undefined){
            params['huowumingcheng'] = '%' + this.searchForm.huowumingcheng + '%'
          }
          if(this.searchForm.huowuleixing!='' && this.searchForm.huowuleixing!=undefined){
            params['huowuleixing'] = this.searchForm.huowuleixing
          }
          if(this.searchForm.gongyingshang!='' && this.searchForm.gongyingshang!=undefined){
            params['gongyingshang'] = this.searchForm.gongyingshang
          }
          if(this.searchForm.cangkuhao!='' && this.searchForm.cangkuhao!=undefined){
            params['cangkuhao'] = this.searchForm.cangkuhao
          }
			let user = JSON.parse(this.$storage.getObj('userForm'))
			console.log(user)
			this.$http({
				url: "huowuxinxi/page",
				method: "get",
				params: params
			}).then(({ data }) => {
				if (data && data.code === 0) {
					this.dataList = data.data.list;
					this.totalPage = data.data.total;
				} else {
					this.dataList = [];
					this.totalPage = 0;
				}
				this.dataListLoading = false;
			});
    },
    // 每页数
    sizeChangeHandle(val) {
      this.pageSize = val;
      this.pageIndex = 1;
      this.getDataList();
    },
    // 当前页
    currentChangeHandle(val) {
      this.pageIndex = val;
      this.getDataList();
    },
    // 多选
    selectionChangeHandler(val) {
      this.dataListSelections = val;
    },
    // 添加/修改
    addOrUpdateHandler(id,type) {
      this.showFlag = false;
      this.addOrUpdateFlag = true;
      this.crossAddOrUpdateFlag = false;
      if(type!='info'){
        type = 'else';
      }
      this.$nextTick(() => {
        this.$refs.addOrUpdate.init(id,type);
      });
    },
	huowuxinxistatusChange(e,row){
		if(row.status==0){
			row.passwordwrongnum = 0
		}
		this.$http({
			url:'huowuxinxi/update',
			method:'post',
			data:row
		}).then(res=>{
			if(row.status==1){
				this.$message.error('该用户已锁定')
			}else{
				this.$message.success('该用户已解除锁定')
			}
		})
	},
    // 删除
    async deleteHandler(id ) {
		var ids = id? [Number(id)]: this.dataListSelections.map(item => {
			return Number(item.id);
		});
		await this.$confirm(`确定进行[${id ? "删除" : "批量删除"}]操作?`, "提示", {
			confirmButtonText: "确定",
			cancelButtonText: "取消",
			type: "warning"
		}).then(async () => {
			await this.$http({
				url: "huowuxinxi/delete",
				method: "post",
				data: ids
			}).then(async ({ data }) => {
				if (data && data.code === 0) {
					this.$message({
						message: "操作成功",
						type: "success",
						duration: 1500,
						onClose: () => {
							this.search();
						}
					});
            
				} else {
					this.$message.error(data.msg);
				}
			});
		});
    },


  }

};
</script>
